import React, {useState} from 'react';
import {MdKeyboardArrowLeft, MdKeyboardArrowRight} from 'react-icons/md';

import styles from './Slider.module.css';

const Slider = props => {
    const length = props.slides.length;
    const [current, setCurrent] = useState(length - 1);

    console.log('length: ' + length)

    const nextSlide = () => {
        setCurrent(current < length - 1 ? current + 1 : current);
    }

    const prevSlide = () => {
        setCurrent(current === 0 ? current : current - 1);
    }

    let leftArrow = null;
    // let leftArrow = <MdKeyboardArrowLeft className={styles.leftArrow} style={{visibility: "hidden"}} onClick={prevSlide} />
    
    let rightArrow = null;

    // if(!Array.isArray(props.slides) || props.slides <= 0) {
    //     return null;
    // }

    return (
        <div className={styles.slider}>
            {
                leftArrow = (current > 0) ? 
                    <MdKeyboardArrowLeft className={styles.leftArrow} 
                    onClick={prevSlide} />
                    :
                    <MdKeyboardArrowLeft className={styles.leftArrow}
                    style={{opacity: 0.5}} onClick={prevSlide} />
                    
            }
            <div className={styles.slide}>
                {props.slides.map((slide, index) => {
                    return (
                        <div
                            className= {[
                                styles.slideContent,
                                index === current ? styles.active : ''
                            ].join(" ")
                            }
                            key={index}
                        >
                            {index === current && (slide)}
                        </div>
                    )
                })}
            </div>
            {
                rightArrow = (current === length - 1) ?
                <MdKeyboardArrowRight className={styles.rightArrow}
                    style={{opacity: 0.5}} onClick={nextSlide} />
                    :
                    <MdKeyboardArrowRight className={styles.rightArrow} 
                    onClick={nextSlide} />
            }
        </div>
    )
}

export default Slider;