import React, { useState } from 'react';

import styles from './SkillLogo.module.css';

const SkillLogo = (props) => {
    const [isHovered, setIsHovered] = useState(false);
    const logoStyle = isHovered ? styles.LogoHovered : styles.Logo;
    const skillNameStyle = isHovered ? styles.SkillNameShown : styles.SkillNameHidden;

    return (
        <div className={styles.Container} style={{gridArea: props.name}}>
            <img 
                src={props.logoImage} 
                alt="Skill" 
                className={logoStyle}
                onMouseEnter={() => setIsHovered(true)}
                onMouseLeave={() => setIsHovered(false)}
            />
            <div className={skillNameStyle} style={{overflow: "hidden"}}>
                {props.name}
            </div>
        </div>
    )
}

export default SkillLogo;