import React, { useRef, useEffect } from 'react';
import { openSimplexNoise } from '../../../../utils/OpenSimplexNoise/SimplexNoise';
import { noise } from '@chriscourses/perlin-noise';
import { createChain, updateNodes } from './chainEffect';

const randomIntFromRange = (min, max) => 
    Math.floor(Math.random() * (max - min + 1) + min);

const drawRhombus = (ctx, xCenter, yCenter, sideLength, rotation, color) => {
    const numberOfSides = 4;
    // uncomment to enter rotation in degrees. 
	// rotation *= Math.PI/180;

    ctx.beginPath();
    ctx.moveTo(
        xCenter + sideLength * Math.cos(rotation), 
        yCenter + sideLength * Math.sin(rotation)
    );
    ctx.lineTo(
        xCenter + (sideLength - 10) * Math.cos(rotation + (1 * 2 * Math.PI / numberOfSides)),
        yCenter + (sideLength - 10) * Math.sin(rotation + (1 * 2 * Math.PI / numberOfSides))
    );    
    ctx.lineTo(
        xCenter + (sideLength + 10) * Math.cos(rotation + (2 * 2 * Math.PI / numberOfSides)),
        yCenter + (sideLength + 10) * Math.sin(rotation + (2 * 2 * Math.PI / numberOfSides))
    );
    ctx.lineTo(
        xCenter + (sideLength - 10) * Math.cos(rotation + (3 * 2 * Math.PI / numberOfSides)),
        yCenter + (sideLength - 10) * Math.sin(rotation + (3 * 2 * Math.PI / numberOfSides))
    );

    ctx.strokeStyle = 'blue';
    ctx.stroke();
    ctx.fillStyle = color;
    ctx.fill();
    ctx.closePath();
}

const distance = (xA, yA, xB, yB) => {
    const horizontalDistance = (xA - xB);
    const verticalDistance = (yA - yB);

    return Math.sqrt(
        Math.pow(horizontalDistance, 2) + 
        Math.pow(verticalDistance, 2)
    );
}

const polarToCartesian = (angle, distance) => {
    return {
        x: distance * Math.cos(angle),
        y: distance * Math.sin(angle)
    }
}

const cartesianToPolar = (x1, y1, x2, y2) => {
    const x = x2 - x1;
    const y = y2 - y1;

    return {
        distance: Math.sqrt(Math.pow(2, x) + Math.pow(2, y)),
        angle: Math.atan2(y, x)
    };
}

const degreesToRadians = (degrees) => {
    return Math.PI / 180;
}

const perlinNoiseMotion = (parameter) => {
    const offset = new Date() * 0.000005;

    return {
        x : noise(parameter + offset + 20) * window.innerWidth,
        y : noise(parameter + offset) * window.innerHeight
    } 
}

const eightFigureMotion = (parameter) => ({
    x : Math.cos(parameter),
    y : Math.sin(2 * parameter) / 2
});

const changeNumberSign = (sign, number) => {
    if (sign === 0) {
        sign = 1;
    }
    else if (sign === -0) {
        sign = -1;
    }

    return sign * number;
}

const rhombusLengths = [20, 25, 30, 35, 40];
const kiteColors = [
    '#F27405',
    '#0487D9',
    '#F2CB05',
    '#049DD9',
    '#D9042B'
];

const kite = createChain(rhombusLengths, window.innerHeight, kiteColors);
const kiteHead = kite[kite.length - 1];

kite.forEach(node => {
    console.log(node)
})

let previousRotationAngle = 0;
let previousX = 0;
let previousY = 0;
let sign = 1;

const draw = (ctx, speed) => {
    ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
    // ctx.fillStyle = 'white';
    // ctx.fillRect(0, 0, ctx.canvas.width, ctx.canvas.height);

    const offset = new Date() * 0.000005;

    const noiseX = noise(speed + offset + 50);
    const noiseY = noise(speed + offset);

    const rotationAngle = noise(speed) * Math.pow(2, Math.PI);
    // const rotationAngle = 3.14159;

    let rhombusX = (Math.cos(speed + 1) * 800) + Math.cos(noiseX * 80) * 100;
    let rhombusY = (Math.sin(speed) * ctx.canvas.height) + Math.sin(noiseY * 70)  * 100;

    if ((rhombusX >= ctx.canvas.width - 70) || (rhombusX <= 70)) {
        rhombusX = changeNumberSign(Math.sign(rhombusX), rhombusX);
    }

    if ((rhombusY >= ctx.canvas.height - 70) || (rhombusY <= 70)) {
        rhombusY = changeNumberSign(Math.sign(rhombusY), rhombusY);
    }

    kiteHead.x = rhombusX;
    kiteHead.y = rhombusY;
    updateNodes(kite);


    for (let i = 0; i < kite.length; i++) {
        const angle = cartesianToPolar(previousX, previousY, rhombusX, rhombusY).angle;

        drawRhombus(
            ctx, 
            kite[i].x,
            kite[i].y,
            kite[i].size, 
            angle,
            kiteColors[i]
        );
    }

    previousRotationAngle = rotationAngle;
    previousX = rhombusX;
    previousY = rhombusY;
}

const CanvasAnimation = props => {
    const canvasRef = useRef(null);

    useEffect(() => {        
        const canvas = canvasRef.current;
        const ctx = canvas.getContext('2d');

        canvas.width = window.innerWidth;
        canvas.height = window.innerHeight;

        let animationFrameId = null;
        let speed = 5;
        const simplexSeed = Date.now();
        
        const render = () => {            
            speed += 0.003;
            draw(ctx, speed);
            animationFrameId = requestAnimationFrame(render);
        };
        render();

        return() => {
            cancelAnimationFrame(animationFrameId);
        }

    });
    

    return (
        <canvas 
            ref={canvasRef} 
            {...props} 
            style={
                {zIndex: "1", position: "absolute"}
            }>
        </canvas>
    )
};

export default CanvasAnimation;