const createChain = (rhombusLengths, canvasHeight, colors) => {
    const nodes = [];

    for (let i = 0; i < rhombusLengths.length; i++) {
        nodes.push({
            x: (i * (rhombusLengths[i] * 2)),
            y: canvasHeight / 2,
            size: rhombusLengths[i],
            color: colors[i],
            head: null
        });

        // assign head for each node, except the rood node
        if (nodes[i - 1]) {
            nodes[i - 1].head = nodes[i];
        }
    }

    return nodes;
}

const updateNodes = (nodes) => {
    for (let i = 0; i < nodes.length - 1; i++) {
        const node = nodes[i];

        let dx = node.head.x - node.x;
        let dy = node.head.y - node.y;

        const distance = Math.sqrt(dx * dx + dy * dy);
        const sumRadii = node.size + node.head.size;
        const nodeGap = 0.1;

        // if (distance > sumRadii * nodeGap) {
            // get unit Vector x component (direction)
            dx /= distance;
            // get unit Vector y component (direction)
            dy /= distance;
            // update x position of each node
            node.x += dx * (distance * 0.1);
            // update y position of each node
            node.y += dy * (distance * 0.1);
        // }
    }
}


export { createChain, updateNodes };